import {DEFAULT_URL} from "../index";
import Cookies from "js-cookie";

const mainMiddleware = store => next => action => {
    switch (action.type) {
        case "APP_LOGOUT": {
            let req = new XMLHttpRequest();
            req.open("GET", `${DEFAULT_URL}/logout`, true);
            req.send();
            window.localStorage.setItem("user", null);
            action.value.history.push("/login");
            return next(action)
        }
        case "APP_GET_POINTS": {
            getPoints(store, action.value.history);
            return next(action)
        }
        case "MAIN_ADD_POINT": {
            addPoint(store, action.value);
            store.dispatch({type: "MAIN_SET_Y", value: ""});
            return next(action)
        }
        case "APP_UPDATE_POINTS_SUCCESS": {
            updateDrawing(store, action.value, store.getState().mainState.rField);
            return next(action);
        }
        case "MAIN_SET_R": {
            if(Number(action.value) === 0){
                updateDrawing(store, store.getState().appState.points, 0);
                return next(action);
            }
            if( Number(action.value)<0 || Number(action.value)>5){
                updateDrawing(store, store.getState().appState.points, String(action.value)[0]);
                return next(action);
            }
            updateDrawing(store, store.getState().appState.points, Number(action.value));
            return next(action);
        }
        case "MAIN_SET_CURRENT_POINT": {
            if (action.value.id !== 0) {
                let history = action.value;
                store.dispatch({type: "MAIN_SET_CHANGE_X", value: "" + history.x});
                store.dispatch({type: "MAIN_SET_CHANGE_Y", value: "" + history.y})
            } else {
                store.dispatch({type: "MAIN_SET_CHANGE_X", value: ""});
                store.dispatch({type: "MAIN_SET_CHANGE_Y", value: ""})
            }
            return next(action);
        }

        case "MAIN_DELETE_POINT": {
            deletePoint(store, action.value);
            store.dispatch({type: "MAIN_SET_CURRENT_POINT", value: {id: 0}});
            return next(action);
        }
        default:
            return next(action);
    }
};

const updateDrawing = (store, value, r) => {
    let array = [];
    for (let i of value) {
        let x = i.x;
        let y = i.y;
        array.push({
            x: x,
            y: y,
            ch: ((x >= 0 && x <= r && y >= 0 && y <= r) || (x <= 0 && y >= 0 && x * x + y * y <= r / 2 * r / 2) || (x >= 0 && y <= 0 && r >= x - y))
        });
    }
    store.dispatch({type: "APP_UPDATE_DRAWING_SUCCESS", value: array});
};

const getPoints = (store, history) => {
    let req = new XMLHttpRequest();
    let page=Cookies.get("p");
    req.open("GET", `${DEFAULT_URL}/points?page=${page-1}&size=5`, true);
    req.onload = () => handleUpdate(req.responseText, store, history);
    req.onerror = () => alert("Сервер временно недоступен");
    req.send();
};

const addPoint = (store, value) => {
    let req = new XMLHttpRequest();
    let page=Cookies.get("p");
    req.open("POST", `${DEFAULT_URL}/points?page=${page-1}&size=5`, true);
    req.onload = () => handleUpdate(req.responseText, store);
    req.onerror = () => alert("Сервер временно недоступен");
    req.setRequestHeader('Content-Type', 'application/json');
    req.send(JSON.stringify(value));
};



const deletePoint = (store, value) => {
    let req = new XMLHttpRequest();
    req.open("DELETE", `${DEFAULT_URL}/points`, true);
    req.onload = () => {
        handleUpdate(req.responseText, store);
        store.dispatch({type: "MAIN_SET_CURRENT_POINT", value: {id: 0}})
    };
    req.onerror = () => alert("Сервер временно недоступен");
    req.setRequestHeader('Content-Type', 'application/json');
    req.send(JSON.stringify({id: value.id}));
};

const handleUpdate = (text, store, history = null) => {
    if (text.startsWith("{")) {
        let response = JSON.parse(text);
        if (response.status === 200) {
            store.dispatch({type: "APP_UPDATE_POINTS_SUCCESS", value: response.answer.content})
            store.dispatch({type:"APP_UPDATE_TOTALPAGES",value:response.answer.totalPages})
        } else if (history) {
            alert(history);
            store.dispatch({type: "APP_LOGOUT", value: {history: history}})
        }
    } else store.dispatch({type: "APP_LOGOUT", value: {history: history}})
};



export default mainMiddleware;